/********* General Styling  *********/

body {
  font-family: "Frutiger-Roman";
  margin: 0;
  background-color: #fff6e6;
  color: #52473f;
}
@font-face {
  font-family: "Frutiger-Roman";
  src: url("./fonts/FrutigerLTStd-Roman.otf") format("woff2");
} 

@font-face { 
  font-family: "Frutiger-Bold";
  src: url("./fonts/FrutigerLTStd-Bold.otf") format("woff2");
}

@font-face {
  font-family: "Frutiger-Light";
  src: url("./fonts/FrutigerLTStd-Light.otf") format("woff2");
}

/********* Nav Styling  *********/
nav {
  color: #53463f;
}

.google-link {
  text-decoration: none;
  color: inherit;
}
#top-nav-div,
#bottom-nav-div {
  display: flex;
  width: 100%;
}

#bottom-nav-div {
  margin-bottom: 10px;
}

#top-nav-div {
  flex-direction: row;
  justify-content: flex-end;
}

#top-nav-div p{
margin-bottom: 0;
font-size: 1.2em;
}

#phone-number-div {
  margin-right: 10vw;
  margin-left: 5vw;
}

#logo-div {
  width: 30%;
  margin-left: 10%;
  margin-right: 20%;
  display: flex;
  align-items: center;
}

#logo-img {
  width: 20vh;
}

#menu-link {
  grid-area: menu-link;
}

#bakery-link {
  grid-area: bakery-link;
}

#gallery-link {
  grid-area: gallery-link;
}

#menu-highlight {
  grid-area: menu-highlight;
  margin-left: 30px;
}

#bakery-highlight {
  grid-area: bakery-highlight;
  margin-left: 30px;
}

#gallery-highlight {
  grid-area: gallery-highlight;
  margin-left: 30px;
}

#nav-right-div {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 50px 10px;
  grid-template-areas: 
    "menu-link bakery-link gallery-link"
    "menu-highlight bakery-highlight gallery-highlight";
  width: 30%;
  margin-right: 10%;
  /* display: flex; */
  justify-items: center;
  align-items: end;
}
#nav-right-div a {
  width: 33%;
  /* text-align: right; */
}

#nav-right-div a, .menu-options-div a{
  text-decoration: none;
  color: inherit;
  font-size: 1.2em;
  font-family: "Frutiger-Bold";
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
}

#nav-right-div a:hover, .menu-options-div a:hover{
  color: #45e6df;
}

#menu-highlight, #bakery-highlight, #gallery-highlight {
  background-color:  #e87427;
  width: 80%;
  height: 100%;
  text-align: right;
}
/********* Homepage Gallery Styling  *********/

#gallery-div {
  min-height: 63vh;
  margin-left: 10vw;
  margin-right: 10vw;
}

.slider {
  width: 80vw;
}

.slider-images {
  height: 500px;
  width: 100%;
}

.carousel-dots li {
  background-color: black;
  border-radius: 10px;
  border-width: 1px;
}

.carousel-dots li.carousel-dots-active button:before {
  opacity: 1 !important;
  color: #e87427 !important;
}

.bottom-left-headline {
  font-family: 'Frutiger-Bold';
  font-size: 36px;
  color: #ffffff;
  position: absolute;
  bottom: 116px;
  left: 60px;
}

.bottom-left-text{
  font-family: 'Frutiger-Bold';
  font-size: 26px;
  text-decoration: none;
  color: #45e6df;
  position: absolute;
  bottom: 77px;
  left: 60px;
}

.bottom-left-text:hover {
  color: #8de2de;
}

/********* Menu Pages Styling  *********/
#brunch-hero-img {
  margin-left: 10vw;
  margin-right: 10vw;
  height: 90px;
  width: 80vw;
  background-image: url("https://res.cloudinary.com/dcagc4c0j/image/upload/v1574110900/photo-title-brunch_vyktrk.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position:center;
}

#brunch-hero-text {
  font-family: "Frutiger-Roman";
  color: white;
  font-size: 1.5em;
  font-weight: bold;
  position: absolute;
  top: 16vh;
  left: 15vw;
  margin-bottom:0;
}

#brunch-menu-div {
  margin-bottom: 5vw;
}

.menu-description-div {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.menu-options-div {
  display:flex;
  flex-direction: row;
  justify-content: space-between;
  align-content: center;
  margin: 3vw 10vw 0vw 12vw;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
}

.menu-blurb-div p{
  margin-left: 5vw;
  margin-right: 10vw;
  margin-bottom: 0;
  font-family: "Frutiger-Roman";
  font-size: 1.2em;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.58;
  letter-spacing: normal;
}

.dish-category-h2 {
  margin-left: 12vw;
  font-family: 'Frutiger-Bold';
  font-size: 1.8em;
  color: #e87427;
}
.menu-items-div {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.left-menu-item{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-left: 15vw;
  margin-right: 5vw;
}
.right-menu-item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-left: 5vw;
  margin-right: 5vw;
}

.menu-item h3 {
  font-family: 'Frutiger-Roman';
  font-size: 1.2em;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.08;
  letter-spacing: normal;
  color: var(--greyish-brown);
}

.menu-items-right-div {
  margin-right: 10vw;
}

.menu-items-left-div p {
  margin: 0 5vw 2vw 15vw;
  font-family: 'Frutiger-Light';
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.08;
  letter-spacing: normal;
  color: var(--greyish-brown);
}

.menu-items-right-div p {
  margin: 0 5vw 2vw 5vw;
  font-family: 'Frutiger-Light';
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.08;
  letter-spacing: normal;
  color: var(--greyish-brown);
}
/********* Bottom-Footer Styling  *********/
#bottom-homepage-div {
  background-color: #52473f;
  color: #f8f7f7;
  min-height: 60vh;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

#bottom-homepage-div h2{
  font-family: "Frutiger-Bold";
  font-size: 1.8em;
  color: #e87427;
  margin-top: 5vh;
}

.bigger-p {
  font-family: "Frutiger-Bold";
  font-size: 1.4em;
}

.smaller-p {
  font-size: 1.1em;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.58;
  letter-spacing: normal;
}

#delivery-info-div {
  margin-bottom: 10vh;
}

.delivery-link {
  color: #45e6df;
  text-decoration: none;
}


#bottom-left-div {
  margin-left: 10vw;
  margin-right: 5vw;
}

#bottom-right-div {
  margin-left: 5vw;
  margin-right: 10vw;
}

#bottom-right-div p {
  display: block;
}

#hours-h2,
#brunch-h2 {
  margin-bottom: 1vh;
}

#copyright-info {
  font-size: 0.7em;
  margin-bottom: 10vh;
}

/* ===========gallery page styling=========== */

#gallery-top-image-container{
  position: relative;
  color: #ffffff;
}

#gallery-top-image {
  width: 80vw;
  margin-left: 10vw;
  margin-right: 10vw;
}

#galler-top-image-text{
  font-family: 'Frutiger-Bold';
  font-size: 30px;
  position: absolute;
  bottom: 1vh;
  left: 15vw;
}

#left-gallery-top {
  margin-top: 80px;
  margin-bottom: 80px;
  display: grid;
  grid-template-columns: 260px 260px;
  grid-template-rows: 260px;
  grid-column-gap: 80px;
  grid-row-gap: 80px;
}

.popup-content {
  border: none !important;
  background :  none !important;
}

#right-gallery-top {
  margin-bottom: 80px;
  display: grid;
  grid-template-columns: 260px 260px;
  grid-template-rows: 260px;
  grid-column-gap: 80px;
  grid-row-gap: 80px;
}

.small-gallery-images-top {
  height: 260px;
  width: 260px;
  cursor: pointer;
  transition: 0.3s;
}

.modal-pics {
  height: 600px;
  width: 600px;
}

.small-gallery-images-top:hover {
  opacity: 0.7;
}

.small-gallery-images-top-right {
  width: 260px;
  cursor: pointer;
  transition: 0.3s;
}

.small-gallery-images-top-right:hover {
  opacity: 0.7;
}

.large-gallery-image-left {
  margin-bottom: 80px;
  height: 600px;
  width: 600px;
  cursor: pointer;
  transition: 0.3s;
}

.large-gallery-image-left:hover {
  opacity: 0.7;
}

.large-gallery-image-right {
  margin-bottom: 80px;
  height: 600px;
  width: 600px;
  cursor: pointer;
  transition: 0.3s;
}

.large-gallery-image-right:hover {
  opacity: 0.7;
}

#left-gallery-bottom {
  margin-bottom: 80px;
  display: grid;
  grid-template-columns: 260px 260px;
  grid-template-rows: 260px 260px;
  grid-column-gap: 80px;
  grid-row-gap: 80px;
}

#right-gallery-bottom {
  margin-bottom: 80px;
  display: grid;
  grid-template-columns: 260px 260px;
  grid-template-rows: 260px 260px;
  grid-column-gap: 80px;
  grid-row-gap: 80px;
}

.small-gallery-images-bottom {
  height: 260px;
  width: 260px;
  cursor: pointer;
  transition: 0.3s;
}

.small-gallery-images-bottom:hover {
  opacity: 0.7;
}

#gallery {
  display: flex;
  flex-direction: row;
  margin-left: 10vw;
  margin-right: 10vw;
}

#gallery-text {
  font-family: "Frutiger-Roman";
  width: 600px;
  height:120px;
  font-size: 24px;
  margin-top: 10vh;
  margin-bottom: 7vh;
  margin-right: 5vw;
}

#right-gallery {
  margin-left:5vw;
  margin-right:5vw;
}


/* =========media query=========== */


@media only screen and (max-width: 600px) {

  html,body {
    overflow-x: hidden;
  }

  .slider-images {
    width: 320px;
    height: 280px;
  }

  .slider {
    width: 80vw;
  }

  #top-nav-div{
  
  display: flex;
  width: 104%;
  height: 60px;
}

#bottom-nav-div{
  flex-direction: row;
  justify-content: space-evenly;
}

  #address-div p{
  width: 120px;
  height: 28px;
  font-family: "Frutiger-Roman";
  src: url("./fonts/FrutigerLTStd-Roman.otf") format("woff2");
  font-size: 12px;
  text-align: left;
  color: var(--greyish-brown);
  margin-top: 20px;
  /* position: relative;
  top: 17px;
  left: 20px; */
  
  }

  .dot{
    visibility: hidden;
  }

  #phone-number-div {
    height: 20px;
    font-family: "Frutiger-Roman";
    font-size: 12px;
    margin-top: 20px;
    margin-right: 5vw;
    margin-left: 0;
  }

  #logo-div {
    margin-right: 0;
    margin-left:0;
  }

  #logo-img{
    
    position: absolute;
    top: 13px;
    left: 4px;
    width: 100px;
  }

  #nav-right-div {
    margin-right: 0;
  }

  #nav-right-div a{
    position: relative;
    right: 170px;
    margin-left: 20px;
  }

  .carousel-track {
    height: 270px;
  }

  .slider {
    width: 127%;
    position: relative;
    right: 35px;
  }

div#gallery-div{
    min-height: 20vh;
    position: relative;
    
  }

  .slider-images {
    width: 100%;
    object-fit: cover;
    
  }

  /******* MOBILE FOOTER STYLING *******/
  p.bottom-left-headline{
    font-family: "Frutiger-Roman";
    src: url("./fonts/FrutigerLTStd-Roman.otf") format("woff2");
    font-size: 24px;
    font-weight: bold;
    position: relative;
   bottom: 150px;
    left: 20px;
   
  }

  a.bottom-left-text{
    width: 200px;
    font-family: "Frutiger-Roman";
    src: url("./fonts/FrutigerLTStd-Roman.otf") format("woff2");
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 40px;
    margin-left: -40px;
    
  }

  #bottom-homepage-div{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    /* justify-content: center; */
  }

  #bottom-homepage-div h2{ 
  font-size: 24px;
  margin-top: 0;
  }

  #delivery-info-div {
    margin-bottom: 0;
  }

  .delivery-link {
    display: block;
  }

.bigger-p{
  font-size: 18px;
}

p.smaller-p{
  font-size: 15px;
}

.desktop-only{
  display: none;
}

#bottom-left-div {
  margin: 8vw 20px 0 20px;
}

#bottom-right-div {
  margin: 0vw 20px 8vw 20px;
}

#bottom-right-div p.smaller-p{
  position: relative;
  /* padding-top: -20px;;
  left: 20px; */
}

/* MOBILE MENU STYLING *********/

h2#brunch-h2{
  left: 20px;
}


#brunch-hero-img {
  height: 30vw;
  width: 100%;
  background-image: url("https://res.cloudinary.com/dcagc4c0j/image/upload/v1574110900/photo-title-brunch-mobile_lrkh9b.jpg");
  background-size: unset;
  margin: 0px;
}

#brunch-hero-text {
  top: 20vh;
  left: 5vw;
}

.dish-category-h2 {
  margin:20px;
  font-size: 28px;
}

p.menu-items-left-div,p.menu-items-right-div {
  align-items: center;
  margin: 0px;
}


.menu-items-right-div {
  margin-right: 0;
} 

.menu-items-div {
  margin-right: 20px;
  margin-left: 20px;
  display: flex;
  flex-direction: column;
}

.menu-description-div {
  display: flex;
  flex-direction: column-reverse;

}


.menu-blurb-div p {
  margin: 20px;
  font-size: 14px;
}

.right-menu-item, .left-menu-item {
margin: 0px;
}

.right-menu-item h3, .left-menu-item h3 {
  margin-bottom: 7px;
  font-size: 20px;
  }

.menu-items-div p {
margin: 0px;
}


}


